import * as React from 'react';

import CanvasControlType from './CanvasControlType';
import CanvasStore from '../canvasComponent/CanvasStore';
import { ISerializedCanvasControl, ICanvasControl } from '../canvasControl/ISerializedCanvasControl';
import SPRte, { ISPRteProps } from '../../../sp-rte/rte/SPRte';
import WebPartZone, { IWebPartZoneProps } from '../../../canvas/sampleCanvasControls/WebPartZone';

export default class CanvasControlFactory {
  public static CreateCanvasControl(props: ISerializedCanvasControl, store: CanvasStore): JSX.Element {
    (props as ICanvasControl).onChange = store.notifyChanged;
    (props as ICanvasControl).ref = store.addReference;

    let control: JSX.Element;
    switch (props.controlType) {
      case CanvasControlType.RTE:
        control = React.createElement<ISPRteProps>(SPRte, (props as ISPRteProps));
        break;
      case CanvasControlType.WebPartZone:
        control = React.createElement<IWebPartZoneProps>(WebPartZone, (props as IWebPartZoneProps));
        break;
    }

    return control;
  }
}